import MniejszyHeader from "./MniejszyHeading";
import Link from "../Link";
import Zdjecie from "./Zdjecie";
import zdjAsio from './sdgfh.jpg';
import zdjJd from './jd.jpg'
import Footer from "../komponenty-mainpage/footer";

import { AiFillGithub } from 'react-icons/ai';


function NapisPowitanie(props) {
    return <h1 className=" m-4 text-center text-2xl font-semibold">{props.tekst}</h1>
}

function PoleCPP() {
    return (
        <div className="grid grid-cols-2 md:ml-0 md:grid-cols-3">
            <div id="lewo" className="hidden md:block">\

            </div>
            <div className="m-4">
                <div id="glowneOkno" className="my-2">
                    <NapisPowitanie tekst={"Wisielec LAN przy użyciu ASIO/C++"} />
                    <h1>
                        <Link tekst={`Repozytorium projektu`}
                            link="https://github.com/Destray32/Networking-1"
                            ikonka={<AiFillGithub className="inline-block ml-1 mb-1" size={21} />}
                        />
                    </h1>
                    <MniejszyHeader tekst={"# Wstep"} className={"mt-9"} />
                    <p id="paragrafASIO">
                        Na początku chciałbym napomnieć, że jest to bardzo amatorski projekt,
                        który miał za zadanie jedynie sprawdzić proste działanie biblioteki ASIO.
                        Rozwiązania przedstawione w projekcie są bardzo proste i nie do końca efektowne,
                        jest to stricte podłoże pod czystą zabawę 😝.
                        W przyszłości mam zamiar przedstawić "grę" w ładnym GUI. Na celowniku miałem
                        przez jakis czas &nbsp;
                        <Link link={"https://github.com/ocornut/imgui"} tekst={"Dear ImGui"} />.
                    </p>
                    <MniejszyHeader tekst={"# ASIO/C++, a co to w ogóle jest?"} className={"mt-9"} />
                    <p id="paragrafASIO">
                        Po krótce, ASIO to wieloplatformowa biblioteka napisana w języku programowania C++.
                        Używana jest to <b>programowania sieciowego i niskopoziomowych operacji I/O.</b> Jej
                        twórcą jest &nbsp;
                        <Link link={"https://github.com/chriskohlhoff"} tekst="Christopher M. Kohlhoff" />,
                        a repozytorium ASIO może zostać znalezione na profilu, do którego odnosi powyższy link.
                    </p>
                    <Zdjecie zdjecie={zdjAsio} />
                    <p id="paragrafASIO">
                        Bibloteke te mozna sobie wyobrażać jako abstrakcje dla czystego networkingu
                        ze stajni CPP. Ukrywa ona przed nami budowe wymiany informacji
                        i pozwala sie skupic na kreatywnym rozwiązaniu naszej siatki client-serwer.
                    </p>
                    <p id="paragrafASIO">
                        Dla lepszego i szybszego wytłumaczenia działania biblioteki ASIO C++ odsyłam
                        Cię czytelniku, do kogoś mądrzejszego i posiadającego większe doświadczenie
                        niż ja 😋. Mianowicie &nbsp;
                        <Link link={"https://www.youtube.com/c/javidx9"} tekst="javidx9" />
                        &nbsp; posiada całą serię filmików, poświęconych ASIO i budowaniu komunikacji client/serwer
                        dla gier MMO. Serdecznie zapraszam, aby dowiedzieć się wiele ciekawych informacji
                        i nauczyć czegoś nowego.
                    </p>
                    <a href="https://youtu.be/2hNdkYInj4g" >
                        <div className="hover:scale-105 transition-all duration-150 ease-in-out">
                            <Zdjecie zdjecie={zdjJd} />
                        </div>
                    </a>
                    <p id="paragrafASIO">
                        W filmiku poruszane jest wiele ważnych spraw takich jak asynchroniczność
                        w odbieraniu danych ⏰ itp.
                    </p>
                    <Footer className={"mt-4 mb-16"} />



                </div>
            </div>
            <div id="prawo" className="">

            </div>
        </div>
    );
}

export default PoleCPP;